.builder__add.param svg {
    margin-left: 10px;
    fill: #333;
    width: 8px;
    height: 8px;
}
.builder__add.param:hover svg {
    fill: #777;
}
.builder__add.prop {
    margin-top: 10px;
    margin-left: 110px;
}
.builder__add:hover {
    color: #777;
}
.builder .title {
    position: relative;
    font-size: 18px;
    padding-left: 20px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
}
.builder .title:hover svg {
    transform: translateX(3px);
    fill: #777;
}
.builder .title:after {
    content: "";
    position: absolute;
    width: 4px;
    height: 4px;
    border-radius: 99px;
    left: 0;
    top: 50%;
    transform: translateY(-50%);
    background-color: #f35588;
}
.builder .title svg {
    transition: all 0.3s;
    margin-left: 10px;
    fill: #333;
}
.builder__anim-props {
    margin-left: 30px;
    color: #333;
    width: 300px;
    border-radius: 10px;
    padding: 10px;
}
.builder__add-list-outer {
    display: none;
    position: absolute;
    top: 10px;
    left: 10px;
    z-index: 10;
}
.builder__add-list-outer.param {
    top: 10px;
    left: 50px;
}
.builder__add-list-outer.prop {
    top: 40px;
    left: 40px;
}
.builder__add-list-outer.show {
    display: block;
    -webkit-animation: show-list 0.5s forwards;
    animation: show-list 0.5s forwards;
}
.builder__add-list {
    display: block;
    overflow: hidden;
    overflow-y: auto;
    width: 200px;
    max-height: 300px;
    box-shadow: 0 0 50px rgba(0, 0, 0, 0.3);
}
.builder__add-list.prop {
    padding: 20px;
    background-color: #fff;
}
.builder__add-list-item {
    padding: 10px 50px;
    font-size: 15px;
    border-bottom: 1px solid rgba(0, 0, 0, 0.1);
    cursor: pointer;
    transition: all 0.3s;
    background-color: #fff;
    white-space: nowrap;
}
.builder__add-list-item:hover {
    background-color: #f2f3fc;
}
.builder__add-list-item:last-child {
    border-bottom: none;
}
.builder__param-outer {
    position: relative;
}
.builder__param-outer:first-of-type {
    z-index: 10;
}
.builder__param-outer:nth-of-type(2) {
    z-index: 9;
}
.builder__param-outer:nth-of-type(3) {
    z-index: 8;
}
.builder__props-outer {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}
.builder__prop,
.builder__props-outer {
    justify-content: flex-start;
    position: relative;
}
.builder__prop {
    display: inline-block;
    padding: 3px 50px 3px 0;
    margin-top: 10px;
    margin-left: 100px;
    display: flex;
    align-items: center;
}
.builder__prop-t {
    font-weight: 700;
    color: #f35588;
    font-size: 14px;
    min-width: 90px;
}
.builder__prop-p {
    font-size: 14px;
    margin-right: 10px;
    color: #777;
}
.builder__prop-d {
    margin: 0 10px;
}
.builder__prop-inp {
    font-size: 14px;
    width: 70px;
    padding: 2px 10px;
    border-radius: 3px;
}
.builder__unit {
    font-size: 12px;
    font-weight: 700;
    margin-left: 10px;
}
.builder__pref-icon {
    cursor: pointer;
    margin-left: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.builder__pref-icon:hover svg {
    fill: #777;
}
.builder__remove-icon {
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    fill: #777;
}
.builder__remove-icon:hover svg {
    fill: #999;
}
.builder__options {
    padding-right: 20px;
}
.builder__title-group {
    width: 100%;
    display: flex;
    justify-content: flex-start;
    align-items: center;
}
.builder__easing {
    position: relative;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 20px;
    border-bottom: 1px solid #eaeaea;
    margin-top: 60px;
}
.builder__easing-t {
    margin-right: 20px;
    cursor: pointer;
}
.builder__easing-t:hover {
    color: #777;
}
.builder__easing-v {
    margin-right: 10px;
}
.builder__bezier-editor-outer {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: none;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    transition: all 1s;
    background-color: #fff;
    overflow: hidden;
    z-index: 10;
}
.builder__bezier-editor-outer.show {
    display: flex;
    -webkit-animation: showLeftSide 0.5s forwards;
    animation: showLeftSide 0.5s forwards;
}
.builder__bezier-editor-outer.hide {
    -webkit-animation: hideLeftSide 0.5s forwards;
    animation: hideLeftSide 0.5s forwards;
}
.builder__editor-block {
    position: absolute;
    top: 60px;
    left: 0;
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.builder__editor-block svg {
    background-color: transparent;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translateX(-50%) translateY(-50%);
}
.builder__b-e-head {
    position: relative;
    width: 100%;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    z-index: 10;
}
.builder__b-e-head svg {
    cursor: pointer;
    fill: #333;
}
.builder__b-e-head svg:hover {
    fill: #777;
}
.builder__b-e-value {
    position: relative;
    width: 100%;
    font-size: 28px;
    margin-top: 10px;
}
.builder__b-e-reset,
.builder__b-e-value {
    display: flex;
    justify-content: center;
    align-items: center;
}
.builder__b-e-reset {
    position: absolute;
    bottom: 10px;
    left: 10px;
    z-index: 10;
    cursor: pointer;
    background-color: #333;
    color: #fff;
    padding: 5px 30px;
}
.builder__b-e-reset:hover {
    background-color: #444;
}
.builder__dur {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 20px;
    border-bottom: 1px solid #eaeaea;
}
.builder__dur-t {
    width: 120px;
    margin-right: 20px;
    text-align: left;
}
.builder__auto-delay {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding: 10px 20px;
    border-bottom: 1px solid #eaeaea;
}
.builder__update {
    position: absolute;
    top: 30px;
    left: -40px;
    cursor: pointer;
    font-weight: 700;
    background-color: #f35588;
    padding: 10px;
    color: #fff;
    transform: rotate(-90deg);
}
.builder__update:hover {
    background-color: #f685a9;
}
.builder__code {
    margin-top: 50px;
    width: 100%;
    padding-right: 20px;
}
@-webkit-keyframes show-list {
    0% {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}
@keyframes show-list {
    0% {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}
@-webkit-keyframes showLeftSide {
    0% {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}
@keyframes showLeftSide {
    0% {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}
@-webkit-keyframes hideLeftSide {
    0% {
        opacity: 1;
    }
    to {
        opacity: 0;
    }
}
@keyframes hideLeftSide {
    0% {
        opacity: 1;
    }
    to {
        opacity: 0;
    }
}
[data-daws] {
    opacity: 0;
}
#particles-js {
    position: fixed;
    top: 0;
    left: 0;
    display: block;
    width: 100%;
    height: 100%;
    z-index: 1;
}
.dis {
    // opacity: 1 !important;
    opacity: 1;
}
