/* Custom Class */

// title
.heading {
    @extend %heading;
}

.title {
    @extend %title;
}

.subtitle {
    @extend %subtitle;
}

.subtitle-small {
    @extend %subtitle-small;
}

.subtitle-smaller {
    @extend %subtitle-smaller;
}

.subtitle-smallest {
    @extend %subtitle-smallest;
}

// DEscription
.description-large {
    @extend %text-large;
}

.description {
    @extend %text;

    &-large {
        @extend %text-large;
    }

    &-small {
        @extend %text-small;
    }
}

.description-small {
    @extend %text-small;
}

// Font Custom
.tl {
    text-align: left;
}

.tr {
    text-align: right;
}

.tc {
    text-align: center;
}

.tj {
    text-align: justify;
}

// /* Bootstrap Refactory */
// .mx-auto {
//     margin-right: auto;
//     margin-left: auto;
// }


.description {
    ul {
        margin-left: 30px;

        li {
            position: relative;
            &:before {
                content:'';
                position: absolute;
                width: 5px;
                height: 5px;
                background-color: $color-text-dark;
                border-radius: 50%;
                top: 11.5px;
                left: -11.5px;
            }
        }
    }
}